import { baseUrl, prepareHeaders } from "../configApi";

export const paymentConfigureDynamicApi = createApi({
  reducerPath: "paymentConfigureDynamicApi",
  baseQuery: fetchBaseQuery({ baseUrl, prepareHeaders: prepareHeaders }),
  endpoints: () => ({}),
});
